import { useState } from "react";
import "./styles.css";
function App() {
  const [number, SetNumber] = useState(0);
  const increase = () => {
    SetNumber(number + 1);
  };
  const increaseAsyn = () => {
    setTimeout(() => {
      SetNumber(number + 1);
    }, 1000);
  };
  return (
    <div>
      <button onClick={increase}>Increase</button>
      <h1>{number}</h1>
      <button onClick={increaseAsyn}>IncreaseAsync</button>
    </div>
  );
}
export default App;
